import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileCmdListResponse } from "../../Model/Response/FileCmdListResponse";
import { FileCmdResponse } from "../../Model/Response/FileCmdResponse";
import { FileCmdViewModel } from '../../Model/ViewModel/FileCmdViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileCmdServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileCmdServicesProvider extends FileServicesProvider {
  private FileCmds: FileCmdListResponse;
  private FileCmd: FileCmdResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileCmd";
    console.log("Hello FileCmdServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileCmd?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileCmd/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileCmds = new FileCmdListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileCmds;
    });
  }



  public PostPutAsync(FileCmdViewModel:FileCmdViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileCmd";
    else
    sub_url = "FileCmd/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileCmdViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileCmdViewModel,mode).map(
      item => {
        this.FileCmd = new FileCmdResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileCmd;
      }
    );
  }
}



